.nav {
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: (1024px)) {
      position: fixed;
      inset: 0;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      z-index: 500;
      overflow-y: auto;
      width: 100%;
      height: 100%;
      background-color: #fff;
      padding: 200px 40px 15px;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
      @include mobile {
        padding: 100px 15px 15px;

      }
    }
  }
  &__item {
    display: flex;
    align-items: center;
    width: 100%;
    &:not(:last-child) {
      margin: 0 40px 0 0;
    }
    @include tablet {
      &:not(:last-child) {
        margin: 0 0 30px 0;
      }
    }
    @include mobile {
      &:not(:last-child) {
        margin: 0 0 18px 0;
      }
    }
  }
  &__link {
    display: inline-flex;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    white-space: nowrap;
    transition: color 0.3s ease-in-out;
    &::after {
      content: "";
      display: block;
      margin: 0 0 0 20px;
      width: 12px;
      height: auto;
      background-image: url("../img/icons/arrow-link.svg");
      background-position: center center;
      background-repeat: no-repeat;
      display: none;
      @include tablet {
        display: block;
      }
    }
    @include tablet {
      font-size: 24px;
      line-height: 133%;
    }
    &:hover {
      color: #f28f1b;
    }
  }
}
.menu--active {
  .nav__list {
    opacity: 1;
    visibility: visible;
  }
}

.burger {
  @include burger;
}
