.hero {
  position: relative;

  z-index: 10;

  // .hero__wrapper

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100vh;
    min-height: 700px;
    background-color: #fff;
    overflow: hidden;
    max-width: 1920px;
    margin: 0 auto;
    z-index: 1;
  }

  // .hero__bg

  &__bg {
    position: absolute;
    inset: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    img {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
    }
    &::after {
      content: "";
      position: absolute;
      inset: 0;
      background-color: rgba(0, 0, 0, 0.4);
    }
  }

  // .hero__container

  &__container {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  // .hero__body

  &__body {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    z-index: 10;
    margin: 200px 0 100px 0;
    @include tablet {
      justify-content: center;
    }
    @include mobile {
      max-width: none;
    }
  }

  // .hero__title

  &__title {
    font-weight: 700;
    font-size: 84px;
    line-height: 140%;
    color: #fff;
    margin: 0 0 50px 0;
    @include mobile {
      font-size: 34px;
    }
  }

  // .hero__descr

  &__descr {
    font-size: 36px;
    line-height: 140%;
    color: #fff;
    margin: 0 0 20px 0;
    @include mobile {
      font-size: 18px;
    }
  }
  &__descr-2 {
    margin: 0 0 50px 0;
  }

  // .hero__box-button

  &__box-button {
  }

  // .hero__link

  &__link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 60px;
    border-radius: 24px;
    cursor: pointer;
    font-size: 20px;
    font-weight: 700;
    padding: 5px 10px;
    color: #fff;
    background-color: #2570a6;
    box-shadow: 0 0 10px #000;
    transition: box-shadow 0.3s ease-in-out;
    &:hover {
      box-shadow: 0 0 20px #000, 0 0 40px rgba(0, 0, 0, 0.6);
    }
  }
}
