.header {
  position: relative;
  height: 1px;

  // .header__wrapper

  &__wrapper {
    position: absolute;
    width: 100%;
    height: 100px;
    top: 0;
    left: 0;
    background-color: transparent;
    z-index: 10000;
    padding: 52px 0;
    @include mobile {
      padding: 15px 0;
    }
  }
  // .header__container

  &__container {
    display: flex;
  }
  // .header__body

  &__body {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 1000;
    transition: transform 0.3s ease-in-out;
    // img {
    //   width: 226px;
    //   height: 34px;
    // }
    &:hover {
      transform: scale(1.05);
    }
    @include mobile {
      // img {
      //   width: 158px;
      //   height: 24px;
      // }
    }
  }
  &__logo-text {
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;
    color: #fff;
    margin: 0 0 0 50px;
  }
}
