.footer {
  padding: 100px 0;
  // .footer__container

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    @media (max-width: (1100px)) {
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 20px;
    }
    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  // .footer__descr

  &__descr {
  }

  // .footer__title

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: #869ac7;
    text-transform: uppercase;
    margin: 0;
    white-space: nowrap;
  }

  // .footer__text

  &__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 114%;
    color: #869ac7;
    margin: 0;
  }

  // .footer__agreement

  &__agreement {
    display: flex;
    font-weight: 700;
    font-size: 14px;
    line-height: 114%;
    color: #869ac7;
    max-width: 190px;
    margin: 0;
    @include small-tablet {
      max-width: none;
    }

    // &::after {
    //   content: "";
    //   display: block;
    //   margin: 0 0 0 20px;
    //   width: 12px;
    //   height: auto;
    //   background-image: url("../img/icons/arrow-link.svg");
    //   background-position: center center;
    //   background-repeat: no-repeat;
    // }
  }
  &__list {
    @media (max-width: (1100px)) {
      grid-area: 1 / 1 / 2 / 2;
      margin: 0 0 40px 0;
    }
    @include small-tablet {
      grid-area: 1 / 1 / 2 / 2;
    }
  }
}

.sci {
  display: flex;
  gap: 50px;
  @include mobile {
    gap: 40px;
  }
  // .sci__item

  &__item {
  }

  // .sci__link

  &__link {
    display: inline-flex;
    width: 55px;
    height: 55px;
  }
  &__image {
    display: inline-block;
    width: 100%;
    height: 100%;
  }
}
