:root {
  --font-family: "Century Gothic", sans-serif;
  --content-width: 1590px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --mainblack-color: #212f50;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Century Gothic";
  src: url("../fonts/../fonts/centurygothic_regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Century Gothic";
  src: url("../fonts/../fonts/centurygothic_bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  caret-color: transparent;
}

.page__body {
  position: relative;
  margin: 0;
  min-width: 375px;
  min-height: 100%;
  font-size: 16px;
  font-weight: 400;
  color: var(--mainblack-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  color: inherit;
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}
@media (max-width: 1200px) {
  .container {
    max-width: 90%;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: none;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: relative;
  height: 1px;
}
.header__wrapper {
  position: absolute;
  width: 100%;
  height: 100px;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: 10000;
  padding: 52px 0;
}
@media (max-width: 576px) {
  .header__wrapper {
    padding: 15px 0;
  }
}
.header__container {
  display: flex;
}
.header__body {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.header__logo {
  position: relative;
  z-index: 1000;
  transition: transform 0.3s ease-in-out;
}
.header__logo:hover {
  transform: scale(1.05);
}
.header__logo-text {
  font-weight: 700;
  font-size: 18px;
  line-height: 140%;
  color: #fff;
  margin: 0 0 0 50px;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1024px) {
  .nav__list {
    position: fixed;
    inset: 0;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding: 200px 40px 15px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}
@media (max-width: 1024px) and (max-width: 576px) {
  .nav__list {
    padding: 100px 15px 15px;
  }
}
.nav__item {
  display: flex;
  align-items: center;
  width: 100%;
}
.nav__item:not(:last-child) {
  margin: 0 40px 0 0;
}
@media (max-width: 1024px) {
  .nav__item:not(:last-child) {
    margin: 0 0 30px 0;
  }
}
@media (max-width: 576px) {
  .nav__item:not(:last-child) {
    margin: 0 0 18px 0;
  }
}
.nav__link {
  display: inline-flex;
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: #fff;
  white-space: nowrap;
  transition: color 0.3s ease-in-out;
}
.nav__link::after {
  content: "";
  display: block;
  margin: 0 0 0 20px;
  width: 12px;
  height: auto;
  background-image: url("../img/icons/arrow-link.svg");
  background-position: center center;
  background-repeat: no-repeat;
  display: none;
}
@media (max-width: 1024px) {
  .nav__link::after {
    display: block;
  }
}
@media (max-width: 1024px) {
  .nav__link {
    font-size: 24px;
    line-height: 133%;
  }
}
.nav__link:hover {
  color: #f28f1b;
}

.menu--active .nav__list {
  opacity: 1;
  visibility: visible;
}

.burger {
  --burger-width: 30px;
  --burger-height: 18px;
  --burger-line-height: 4px;
  display: none;
}
@media (max-width: 1024px) {
  .burger {
    position: relative;
    display: block;
    border: none;
    padding: 0;
    width: var(--burger-width);
    height: var(--burger-height);
    color: #30529F;
    background-color: transparent;
    cursor: pointer;
    z-index: 10000;
  }
  .burger::before, .burger::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }
  .burger::before {
    top: 0;
  }
  .burger::after {
    top: calc(100% - var(--burger-line-height));
  }
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.hero {
  position: relative;
  z-index: 10;
}
.hero__wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  min-height: 700px;
  background-color: #fff;
  overflow: hidden;
  max-width: 1920px;
  margin: 0 auto;
  z-index: 1;
}
.hero__bg {
  position: absolute;
  inset: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.hero__bg img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}
.hero__bg::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
.hero__container {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.hero__body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  z-index: 10;
  margin: 200px 0 100px 0;
}
@media (max-width: 1024px) {
  .hero__body {
    justify-content: center;
  }
}
@media (max-width: 576px) {
  .hero__body {
    max-width: none;
  }
}
.hero__title {
  font-weight: 700;
  font-size: 84px;
  line-height: 140%;
  color: #fff;
  margin: 0 0 50px 0;
}
@media (max-width: 576px) {
  .hero__title {
    font-size: 34px;
  }
}
.hero__descr {
  font-size: 36px;
  line-height: 140%;
  color: #fff;
  margin: 0 0 20px 0;
}
@media (max-width: 576px) {
  .hero__descr {
    font-size: 18px;
  }
}
.hero__descr-2 {
  margin: 0 0 50px 0;
}
.hero__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 60px;
  border-radius: 24px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  padding: 5px 10px;
  color: #fff;
  background-color: #2570a6;
  box-shadow: 0 0 10px #000;
  transition: box-shadow 0.3s ease-in-out;
}
.hero__link:hover {
  box-shadow: 0 0 20px #000, 0 0 40px rgba(0, 0, 0, 0.6);
}

.footer {
  padding: 100px 0;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
}
@media (max-width: 1100px) {
  .footer__wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 20px;
  }
}
@media (max-width: 767.9px) {
  .footer__wrapper {
    grid-template-columns: repeat(1, 1fr);
  }
}
.footer__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 120%;
  color: #869ac7;
  text-transform: uppercase;
  margin: 0;
  white-space: nowrap;
}
.footer__text {
  font-weight: 700;
  font-size: 14px;
  line-height: 114%;
  color: #869ac7;
  margin: 0;
}
.footer__agreement {
  display: flex;
  font-weight: 700;
  font-size: 14px;
  line-height: 114%;
  color: #869ac7;
  max-width: 190px;
  margin: 0;
}
@media (max-width: 767.9px) {
  .footer__agreement {
    max-width: none;
  }
}
@media (max-width: 1100px) {
  .footer__list {
    grid-area: 1/1/2/2;
    margin: 0 0 40px 0;
  }
}
@media (max-width: 767.9px) {
  .footer__list {
    grid-area: 1/1/2/2;
  }
}

.sci {
  display: flex;
  gap: 50px;
}
@media (max-width: 576px) {
  .sci {
    gap: 40px;
  }
}
.sci__link {
  display: inline-flex;
  width: 55px;
  height: 55px;
}
.sci__image {
  display: inline-block;
  width: 100%;
  height: 100%;
}